<template>
  <div class="Project_product">
    <div id="loadingDiv" v-show="value">
      <div class="loader"></div>
    </div>

    <div id="myModal" class="modal" v-show="modal">
      <!-- Modal content -->
      <div class="modal-content">
        <div class="justify-content-center">
          <p style="margin-bottom: 0">
            <span v-html="text" class="icon-modal"></span>

            <span style="font-size: 20px">{{ msg }}</span>
          </p>
        </div>
      </div>
    </div>

    <!-- <loading :active.sync="isLoading"></loading> -->
    <div class="container" v-if="product.id">
      <nav>
        <ul
          style="
            display: flex;
            list-style: none;
            letter-spacing: 1px;
            padding-left: 0;
          "
        >
          <li>
            <a
              href="#"
              @click.prevent="
                category = '';
                toProducts();
              "
              ></a
            >
          </li>
          <span style="padding: 0 10px 0 10px">/</span>
          <li>
            <a
              href="#"
              @click.prevent="
                category = product.category;
                toProducts();
              "
              >{{ product.category }}
            </a>
          </li>
          <span style="padding: 0 10px 0 10px">/</span>
          <li>{{ product.title }}</li>
        </ul>
      </nav>
      <hr />
      <div class="row mb-5">
        <div class="col-lg-6 col-md-10">
          <div class="d-flex justify-content-center">
            <img
              :src="product.imageUrl[0]"
              alt=""
              class="img-fluid rounded"
              style="object-fit: cover; width: 100%; height: 285px"
            />
          </div>
        </div>
        <div
          class="
            col-lg-6 col-md-10
            d-flex
            justify-content-center
            align-items-center
            prod-right-text
          "
        >
          <div class="card border-0">
            <div>
              <h2 class="card-title text-dark font-weight-bold">
                {{ product.title }}
              </h2>
              <p class="card-text">
                {{ product.content }}
              </p>
              <div class="flex-column align-items-start mb-3">
                <span class="h5 font-weight-bold"> </span>
                <span class="mb-0 h4 font-weight-bold text-danger"> $</span>
                <span class="mb-0 h1 font-weight-bold text-danger">{{
                  product.price
                }}</span>
                <span
                  ><del class="mb-0 mb-2">
                    {{ product.origin_price }}</del
                  ></span
                >
              </div>

              <div class="d-flex">
                <div class="input-group w-50 mr-3">
                  <div class="input-group-prepend">
                    <button
                      class="btn button-add"
                      @click="cartNum--"
                      :disabled="cartNum <= 1"
                      style="cursor: pointer"
                    >
                      <i class="fas fa-minus"></i>
                    </button>
                  </div>
                  <input
                    min="1"
                    class="number-input"
                    readonly
                    v-model="cartNum"
                  />
                  <div class="input-group-append">
                    <button
                      class="btn button-plus"
                      @click="cartNum++"
                      style="cursor: pointer"
                    >
                      <i class="fas fa-plus"></i>
                    </button>
                  </div>
                </div>
                <!-- <button
                  type="button"
                  class="btn btn-primary w-50"
                  @click="addToCart()"
                >
                  
                </button> -->
                <p class="h5 text-right ml-auto">
                   $ {{ product.price * cartNum }}
                </p>
              </div>
              <hr />
              <div class="cart-button-wrapper">
                <a
                  href="javascript:;"
                  onclick=""
                  class="add-to-cart"
                  style="text-decoration: none"
                  @click.prevent="addfavorite()"
                >
                  <span class="hand-cursor empty"
                    ><i class="far fa-heart fa-lg" style="padding: 0;color: #fd5151;"></i> </span
                  ></a
                >
                <a
                  href="javascript:;"
                  class="gtm-addToCart"
                  onclick=""
                  style="text-decoration: none"
                  @click.prevent="addToCart(product.id)"
                >
                  <span class="hand-cursor enrich"></span></a
                >
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div style="background-color: #f0f0f0; padding: 40px 0 40px 0">
      <div class="container">
        <h3 class="h4 mb-4"></h3>

        <swiper :options="swiperOption">
          <swiper-slide
            v-for="item in filterProducts"
            :key="item.id"
            :load="log(filterProducts)"
          >
            <router-link
              :to="`/product/${item.id}`"
              @click.native="haha(item.id)"
              class="text-decoration-none text-dark"
            >
              <div class="card">
                <div class="product__img__wrap">
                  <div
                    class="product__img bg-cover"
                    style="
                      height: 200px;
                      background-position: center;
                      background-size: cover;
                    "
                    :style="{ backgroundImage: `url(${item.imageUrl[0]})` }"
                  >
                    <span
                      class="badge badge-secondary align-self-start favorite"
                      style="border-radius: 0 !important"
                    >
                      {{ item.category }}
                    </span>
                  </div>
                </div>
                <div class="card-body">
                  <div
                    class="d-flex justify-content-between align-items-center"
                  >
                    <h5 class="card-title text-primary font-weight-bold">
                      {{ item.title }}
                    </h5>
                    <!-- <span class="badge badge-secondary align-self-start">
                      {{ item.category }}
                    </span> -->
                  </div>
                  <div class="flex-column align-items-start">
                    <!-- <span class="h5 font-weight-bold"> </span> -->
                    <span class="mb-0 h4 font-weight-bold text-danger"> $</span>
                    <span class="mb-0 h4 font-weight-bold text-danger mr-1">{{
                      item.price
                    }}</span>
                    <span
                      ><del class="mb-0 mb-2">{{
                        item.origin_price
                      }}</del></span
                    >
                  </div>
                </div>
              </div>
            </router-link>
          </swiper-slide>
          <!-- <div class="swiper-pagination" slot="pagination"></div> -->
        </swiper>
      </div>
    </div>
  </div>
</template>

<script>
import "/Users/mandy.liang/demo/node_modules/vue-awesome-swiper/node_modules/swiper/dist/css/swiper.css";
// swiperswiperSlides
import { swiper, swiperSlide } from "vue-awesome-swiper";

export default {
  name: "PageProduct",
  components: {
    swiper,
    swiperSlide,
  },
  data() {
    return {
      msg: "",
      text: "",
      modal: false,
      product: {},
      cartNum: 1,
      test: [],
      value: false,
      favoriteList: [],
      favoriteId: [],
      //--------------SWIPER
      swiperOption: {
        speed: 1200,
        loop: false, //true1
        slidesPerView: 5,
        slidesPerGroup: 1, // 
        spaceBetween: 30, //
        autoplay: {
          delay: 5000,
          disableOnInteraction: true,
        },
        pagination: {
          el: ".swiper-pagination",
          dynamicBullets: true,
          clickable: true,
        },
        breakpoints: {
          992: {
            slidesPerView: 4,
            spaceBetween: 10,
          },
          768: {
            slidesPerView: 2,
            spaceBetween: 15,
          },
        },
      },
    };
  },
  created() {
    this.getProduct();
  },
  methods: {
    log(filterProducts) {
      console.log(filterProducts);
    },
    getProduct() {
      const { id } = this.$route.params;
      this.isLoading = true;
      const api = `https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/product/${id}`;
      this.$http.get(api).then((res) => {
        this.product = res.data.data;
        // console.log(this.product.title)
        // this.getRelatedProducts();
      });

      const all_api = `https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/products?page=1`;
      this.$http.get(all_api).then((res) => {
        this.test = res.data.data;
        console.log(this.test);
      });
    },
    addToCart(id, quantity = 1) {
      const api = `https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/shopping`;
      const cart = {
        product: id,
        quantity,
      };
      this.$http
        .post(api, cart)
        .then(() => {
          this.$emit("updateCart");
          this.text =
            '<i class="fa-solid fa-circle-check" style="color: green;"></i>';
          this.msg = "";
          this.modal = true;
          this.modal = setTimeout(() => {
            this.modal = false;
          }, 1000 * 1 * 1);
        })
        .catch(() => {
          this.text =
            '<i class="fa-solid fa-triangle-exclamation" style="color: red;"></i>';
          this.msg = "";
          // this.msg = errors.response.data.errors[0]; //catcherrors
          this.modal = true;
          this.modal = setTimeout(() => {
            this.modal = false;
          }, 1000 * 1 * 1);
        });
    },
    addfavorite() {
      this.favoriteList.push(this.product);
      this.favoriteId.push(this.product.id);
      this.favoriteList = [...new Set(this.favoriteList)];
      this.favoriteId = [...new Set(this.favoriteId)];
      localStorage.setItem("favoriteList", JSON.stringify(this.favoriteList));
      localStorage.setItem("favoriteId", JSON.stringify(this.favoriteId));
      console.log(localStorage);
      this.text =
        '<i class="fa-solid fa-circle-check" style="color: green;"></i>';
      this.msg = "";
      this.modal = true;
      this.modal = setTimeout(() => {
        this.modal = false;
      }, 1000 * 1 * 1);
    },
    toProducts() {
      this.$router.push(`/products/${this.category}`);
    },
    haha(id) {
      this.$router.push(`/product/${id}`);

      this.value = true;
      this.value = setTimeout(() => {
        this.value = false;
      }, 2500 * 1 * 1);

      window.scrollTo(0, 0);
      this.getProduct();
    },
  },
  computed: {
    filterProducts() {
      console.log("???????????");
      return this.test.filter(
        (item) =>
          item.category === this.product.category && item.id !== this.product.id
      );
    },
    swiper() {
      return this.$refs.mySwiper.swiper;
    },
  },
};
</script>

<style lang="scss" scoped>
.favorite {
  position: absolute;
  left: 0;
}

.swiper-container {
  padding-bottom: 30px;
}

.button-add,
.button-plus {
  border-radius: 50%;
  background-color: #28a745;
  color: #fff;
  width: 30px;
  height: 30px;
  text-align: center;
  padding: 0;
}

.number-input {
  background-color: transparent;
  border: 1px solid #ebeae8;
  width: 83px;
  height: 32px;
  line-height: 32px;
  margin: 0 7px;
  text-align: center;
}

.cart-button-wrapper {
  border-radius: 62px;
  overflow: hidden;
  display: flex;
  border: 1px solid #fd4848;
}

.operation-wrapper .operaion-right .cart-button-wrapper a {
  flex: 1;
  text-align: center;
}

.cart-button-wrapper .empty {
  color: #fd5151;
  padding: 20px 62px;
  flex: 1;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.cart-button-wrapper span {
  display: block;
}

.cart-button-wrapper a {
  flex: 1;
  text-align: center;
}

.cart-button-wrapper .enrich {
  background-color: #fd5151;
  color: #fff;
  padding: 20px 62px;
  flex: 1;
  font-size: 1rem;
  transition: all 0.3s ease;
}

/*LOADING */
.loader,
.loader:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}

.loader {
  margin: auto;
  font-size: 4px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid #4b5f9b;
  border-right: 1.1em solid #4b5f9b;
  border-bottom: 1.1em solid #4b5f9b;
  border-left: 1.1em solid #f8f9fc;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
  transform: translateY(-50%);
  top: 50%;
}

@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

#loadingDiv {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f8f9fc;
  z-index: 3;
  overflow: hidden;
}

.modal {
  display: block;
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
  z-index: 9999999999;
}

/* Modal Content/Box */
.modal-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: #fefefe;
  // margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  max-width: 100%;
}

.icon-modal {
  font-size: 25px;
  vertical-align: middle;
  margin-right: 5px;
}

@media (max-width: 991px) {
  .prod-right-text {
    margin-top: 10px;
  }
}
</style>
